import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { Subject, takeUntil } from 'rxjs';
import { AuthService } from 'src/app/core/auth/auth.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-forget-password',
  templateUrl: './forget-password.component.html',
  styleUrls: ['./forget-password.component.scss']
})
export class ForgetPasswordComponent implements OnInit, OnDestroy{

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  emailInputControl: FormControl = new FormControl('',Validators.required);

  Toast:any;

  showSuccess:boolean =  false;

  constructor(
    private _authService: AuthService,
    private _changeDetectorRef: ChangeDetectorRef,
  ) { 
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });  
  }

  ngOnInit(

  ): void {
  }

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }


  onSubmit(){
    if(this.emailInputControl.valid){
      this.recuperarContraseña();
    }else{
      this.Toast.fire({
        icon: 'error',
        title: 'Debe ingresar un correo electronico valido para continuar'
      });
    }
    // this.emailInputControl.disable();
    // Mark for check
    this._changeDetectorRef.markForCheck();
  }

  recuperarContraseña(){

    if(this.emailInputControl.invalid){
      return;
    }

    this.emailInputControl.disable();

    let data:any = {};
    data.email = this.emailInputControl.value;
    this._authService.resetPassword(data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.showSuccess = true;
        // Mark for check
        this._changeDetectorRef.markForCheck();
      },(error) => {
        this.emailInputControl.enable();
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
        // Mark for check
        this._changeDetectorRef.markForCheck();
      }
    );
  }

}
