import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from "./login/login.component";
import { RegisterComponent } from "./register/register.component";
import { AuthUserTypesResolver } from '../core/auth/auth.resolver';
import { AcountCreatedComponent } from './account-created/account-created.component';
import { RegisterTallerComponent } from './register-taller/register-taller.component';
import { ForgetPasswordComponent } from './forget-password/forget-password.component';

const routes: Routes = [
  { 
    path: 'login', 
    component: LoginComponent
  },
  { 
    path: 'register', 
    component: RegisterComponent 
  },
  { 
    path: 'register-taller', 
    component: RegisterTallerComponent 
  },
  { 
    path: 'account-created', 
    component: AcountCreatedComponent
  },
  { 
    path: 'forget-password', 
    component: ForgetPasswordComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AuthRoutingModule { }
