import { ViewportScroller } from '@angular/common';
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { AuthService } from 'src/app/core/auth/auth.service';
import Swal from 'sweetalert2';

@Component({
  selector: "app-register",
  templateUrl: "./register.component.html",
  styleUrls: ["./register.component.scss"],
})
export class RegisterComponent implements OnInit, OnDestroy {
  @ViewChild("signUpNgForm") signUpNgForm: NgForm;

  signUpForm: FormGroup;

  campos: any = ["nombre", "correo", "contrasenia"];

  types: any = [];

  showAlert: boolean = false;
  messageAlert: string = "";

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  loadingFields: boolean = false;

  fieldsList: any = [];

  maxDate:any = {};

  disableButton: boolean = true;
  
  imgUpload:any ;

  uploadedFiles:any;

  imageBase64: any = '';

  Toast: any;

  activarClases: boolean = false;

  constructor(
    private _formBuilder: FormBuilder,
    private _authService: AuthService,
    private _changeDetectorRef: ChangeDetectorRef,
    private _router: Router,
    private viewScroller: ViewportScroller
  ) {
    let today = new Date();
    this.maxDate.year = today.getFullYear();
    this.maxDate.month = today.getMonth() + 1;
    this.maxDate.day = today.getDate();
    this.Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });
  }

  static patternValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        // if control is empty return no error
        return null;
      }

      // test the value of the control against the regexp supplied
      const valid = regex.test(control.value);

      // if true, return no error (no error), else return error passed in the second parameter
      return valid ? null : error;
    };
  }

  ngOnInit(): void {
    this._authService.userTypes$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((types: any) => {
        // Update the selected agente
        this.types = types;

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });

    this.signUpForm = this._formBuilder.group({
      name: ["", [Validators.required]],
      email: ["", [Validators.required, Validators.email]],
      password: [
        "",
        Validators.compose([
          Validators.required,
          RegisterComponent.patternValidator(/\d/, { hasNumber: true }),
          RegisterComponent.patternValidator(/[A-Z]/, { hasCapitalCase: true }),
          RegisterComponent.patternValidator(/[a-z]/, { hasSmallCase: true }),
          RegisterComponent.patternValidator(
            /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
            { hasSpecialCharacters: true }
          ),
          Validators.minLength(8),
        ]),
      ],
      direccion: ["", [Validators.required]],
      // type: ["", [Validators.required]],
      telefono: ["", [Validators.required, Validators.pattern("[0-9]*")]],
      documento: ["", [Validators.required]],
      fecha_nacimiento: ["", [Validators.required]],
      fotoBase: [""],
      // clienteCampos: this._formBuilder.array([]),
    });
  }

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }


  // -----------------------------------------------------------------------------------------------------
  // @ Public methods
  // -----------------------------------------------------------------------------------------------------

  /**
   * Track by function for ngFor loops
   *
   * @param index
   * @param item
   */
  trackByFn(index: number, item: any): any {
    return item.id || index;
  }

  onFileSelected(event){
    this.uploadedFiles = event.target.files[0];
    if(this.uploadedFiles.type === 'image/jpeg' || this.uploadedFiles.type === 'image/png' || this.uploadedFiles.type === 'image/jpg'){
      const reader = new FileReader();
      reader.readAsDataURL(this.uploadedFiles);
      reader.onload = () => {
        let dataN = reader.result.toString();
        // let dataD = '';
        // dataD = dataN.replace(/^data:image\/\w+;base64,/,'');
        this.imageBase64 = dataN;
      };
    }else{
      this.Toast.fire({
        icon: "error",
        title: 'Debe subir un tipo de imagen valido (JPEG, JPG, PNG).',
      });
      this.signUpForm.get('fotoBase').setValue('');
      this.uploadedFiles = '';
    }
  }

  handleTypeChange(event: any) {
    this.loadingFields = true;
    this.fieldsList.forEach((element, index) => {
      (this.signUpForm.get("clienteCampos") as FormArray).removeAt(0);
    });
    this.fieldsList = [];
    if (event.target.value !== "") {
      this.getFieldsByType(event.target.value);
    }
  }

  getFieldsByType(type: any): void {
    this._authService
      .getFieldsByUserTypes(type)
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(
        (response: any) => {
          this.fieldsList = response;
          this.addFieldsToFormGroup(response);
          this.loadingFields = false;
        },
        (error) => {
          console.log(error);
        }
      );
  }

  addFieldsToFormGroup(fieldsList: any) {

    fieldsList.forEach((element) => {
      let campos: any = {};
      if(element.required){
        campos[element.name] = ["", [Validators.required]];
      }else{
        campos[element.name] = [""];
      }

      // Create an empty calle form group
      const clienteFormGroup = this._formBuilder.group(campos);

      // Add the calle form group to the phoneNumbers form array
      (this.signUpForm.get("clienteCampos") as FormArray).push(clienteFormGroup);
    });
    // Mark for check
    this._changeDetectorRef.markForCheck();
  }

  onSubmit(): void {
    // console.log("object");
    this.showAlert = false;
    // this.disableButton = false;
    if (!this.signUpForm.valid) {
      this.activarClases = true;
      this.messageAlert = "Debes completar los campos para registrarte.";
      this.viewScroller.scrollToPosition([0, 0]);
      this.showAlert = true;
      // this.disableButton = true;
    } else {
      if(!this.signUpForm.disabled){
        this.activarClases = false;
        this.signUp();
      }
    }
  }

  /**
   * Sign up
   */
  signUp(): void {
    // Do nothing if the form is invalid
    if (this.signUpForm.invalid) {
      return;
    }

    // Disable the form
    this.signUpForm.disable();
    
    let user = this.signUpForm.getRawValue();
    user.fecha_nacimiento = this.fdnToString(user.fecha_nacimiento);
    user.fotoBase64 = this.imageBase64;
    delete user.fotoBase;
    
    // Sign up
    this._authService.signUp(user).subscribe(
      (response) => {
        // Navigate to the confirmation required page
        this._router.navigateByUrl("/account-created");
      },
      (response) => {
        // Re-enable the form
        this.signUpForm.enable();
        // Set the alert
        response.error.message.forEach(element => {
          this.messageAlert += element + ',';
        });
        // this.messageAlert = response.error.message;
        this.viewScroller.scrollToPosition([0, 0]);
        // Show the alert
        this.showAlert = true;
        // this.disableButton = true;
      }
    );
  }

  pruebas(): void {
    
    // console.log(this.signUpForm.value);

    let user = this.signUpForm.getRawValue();
    user.fdn = this.fdnToString(user.fdn);
    // user.clienteCampos.forEach(element => {
    //   for (const [key, value] of Object.entries(element)) {
    //     user[key] = value;
    //   }
    // });

    // delete user.clienteCampos;
    console.log(user);
  }

  dateToString(fecha):any{
    var month = (fecha.getMonth() + 1).toString().padStart(2, '0');
    var day = fecha.getDate().toString().padStart(2, '0');
    var date = `${fecha.getFullYear()}-${month}-${day}`;
    return date;
  }

  fdnToString(fecha:any):any{
    return `${fecha.year}-${fecha.month.toString().padStart(2, '0')}-${fecha.day.toString().padStart(2, '0')}`
  }
}
